.wrapper {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: royalblue;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;

  transition: 0.4s;
  cursor: pointer;
}

.wrapper span {
  font-size: 25px;
  font-weight: 700;

  transition: 0.4s;
}

.wrapper:hover {
  background-color: rgb(255, 40, 104);
}

.wrapper:hover span {
  transform: scale(1.4);
}

.user {
  position: absolute;
  top: 10px;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  right: 0;
  font-size: 150px;
  margin: 0;
  padding: 0;
  z-index: 0;
  cursor: default;
  color: rgb(172, 176, 230);
  opacity: 0.2;
  /* filter: blur(0.1rem); */
}

.infoWrapper {
  display: flex;
  align-items: center;
  gap: 20px;
}

.infoWrapper h2 {
  color: #f0f0f0;
}
