form {
  border: 5px solid rgb(238, 240, 241);
  padding: 20px 20px 50px 20px;
  width: 50%;
  border-radius: 5px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.formInput {
  display: block;
  width: 300px;
  padding: 5px 5px;
}

.formLabel {
  font-weight: 400;
  display: block;
  margin-top: 10px;
}

.btn {
  padding: 15px 10px;
  margin-top: 15px;

  border: none;
  display: block;
  width: 300px;

  background-color: #5536e8;
  color: #fff;

  font-weight: 700;

  cursor: pointer;
  border-radius: 5px;

  transition: 0.3s;
}

.btn:hover {
  background-color: #3d27ad;
}
